.btn_orange, .btn_grey {
  font-size: 13px;
  line-height: 30px;
  -webkit-border-radius: 3px;
          border-radius: 3px;
  font-weight: bold;
  cursor: pointer;
  text-align: center;
  -webkit-appearance: none; }

.btn_orange {
  color: #fff;
  border: 1px solid #ff742d;
  background: -webkit-gradient(linear, left top, left bottom, from(#ffa036), to(#ff742d));
  background: -webkit-linear-gradient(top, #ffa036 0%, #ff742d 100%);
  background: -o-linear-gradient(top, #ffa036 0%, #ff742d 100%);
  background: linear-gradient(to bottom, #ffa036 0%, #ff742d 100%); }
  .btn_orange:hover {
    background: #ffa036; }
  .btn_orange:active {
    outline: none;
    background: -webkit-gradient(linear, left bottom, left top, from(#ffa036), to(#ff742d));
    background: -webkit-linear-gradient(bottom, #ffa036 0%, #ff742d 100%);
    background: -o-linear-gradient(bottom, #ffa036 0%, #ff742d 100%);
    background: linear-gradient(to top, #ffa036 0%, #ff742d 100%); }
  .btn_orange:focus {
    outline: none;
    background: #ffa036;
    -webkit-box-shadow: 0px 0px 10px 0px #ffa036;
            box-shadow: 0px 0px 10px 0px #ffa036; }
  .btn_orange:disabled {
    background: #ffa036;
    opacity: 0.5; }
  @media (hover: none) and (pointer: coarse) {
    .btn_orange:hover {
      background: -webkit-gradient(linear, left top, left bottom, from(#ffa036), to(#ff742d));
      background: -webkit-linear-gradient(top, #ffa036 0%, #ff742d 100%);
      background: -o-linear-gradient(top, #ffa036 0%, #ff742d 100%);
      background: linear-gradient(to bottom, #ffa036 0%, #ff742d 100%); } }

